import React from 'react';
import { Route, BrowserRouter, Routes } from 'react-router-dom';

import CoinList from '../CoinList/Loadable';
import CoinDetail from '../CoinDetail/Loadable';

function App() {
    return (
        <BrowserRouter>
            <Routes>
                <Route exact path="/crypto-coin/" element={<CoinList />} />
                <Route exact path="/crypto-coin/coins" element={<CoinList />} />
                <Route path="/crypto-coin/coins/:coinId" element={<CoinDetail />} />
            </Routes>
        </BrowserRouter>
    );
}
export default App;
